  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  text-decoration: none;
}

body {
  background-color: transparent;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  margin: 0;
  text-shadow: 0.4px 0.2 black;
  background-color: rgba(150, 150, 150, 0.2);
}

a, img {
  color: #fbfaf8;
  transition: all 0.5;
}
a:hover, img:hover {
  opacity: 0.7;
  cursor: pointer;
  transform: scale(1.1);
  color: black !important;
}
a:active, img:active {
  transform: scale(1.3);
  color: #a7b7bd;
}
a:focus, img:focus {
  outline: 0;
  color: #a7b7bd;
}

nav {
  display: flex;
  flex-direction: column;
}

.fstHead {
  display: none;
}

#fstHead {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem;
  gap: 1rem;
}

#sndHead {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem;
  gap: 0.2rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  background: #a7b7bd;
  height: 3.2rem;
}

#menuBtn {
  flex: 0.2;
}

#logo {
  flex: 2;
  height: 2.8rem;
}

#labels {
  display: none;
}

.page1 {
  display: flex;
  flex-direction: column;
  margin: 3rem 0.15rem 2rem 0.15rem;
  padding: 0.5rem;
}

.hero {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: flex-start;
}
.hero h1 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 100% 100%;
  background-repeat: no-repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}
.hero .fstPageP {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  border: 3px solid #a7b7bd;
  padding: 0.3rem;
  font-size: 0.9rem;
  text-shadow: 0.4px 0.2 black;
}
.hero h2 {
  margin-top: 1rem;
  font-size: 1.1rem;
}

.page2 {
  display: flex;
  flex-direction: column;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  background-blend-mode: color-burn;
  background-repeat: no-repeat;
}

.Page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 1.5rem;
  gap: 2rem;
  color: #a7b7bd;
}

.fstPart, .contentPart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.fstPart h2, .contentPart h2 {
  margin-bottom: 0.4rem;
}

.figuresH2 {
  color: rgba(87, 85, 85, 0.7);
}

.C1UL, .C2UL {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-columns: 1fr;
  gap: 0.7rem;
}
.C1UL li, .C2UL li {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  background-color: rgba(150, 150, 150, 0.2);
  stroke-opacity: 1;
  padding: 0.3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.3rem;
  max-width: 26rem;
}
.C1UL li img, .C2UL li img {
  flex: 0.4;
  max-width: 10rem;
  max-height: 15rem;
}
.C1UL li h3, .C2UL li h3 {
  flex: 0.4;
  text-align: center;
}
.C1UL li p, .C2UL li p {
  flex: 1;
  text-align: left;
}
.C1UL li .contentPart, .C2UL li .contentPart {
  align-self: stretch;
  align-items: flex-start;
  flex: 1.2;
  gap: 0.2rem;
  margin: 0.5rem 0.2rem;
  color: rgba(87, 85, 85, 0.7);
}
.C1UL li .contentPart h2, .C2UL li .contentPart h2 {
  color: rgba(87, 85, 85, 0.7) !important;
}

.endPart {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  background-color: rgba(87, 85, 85, 0.7);
  color: #a7b7bd;
  white-space: nowrap;
  padding: 1rem;
}

.viewMore {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  align-self: stretch;
  height: 50px;
  background-color: rgba(150, 150, 150, 0.2);
  color: rgba(87, 85, 85, 0.7);
  font-weight: 600;
  border: 0;
  border-radius: 0.1rem;
}

@media (min-width: 375px) and (max-width: 500px) {
  .hero h1 {
    font-size: 1.922rem;
  }
  .hero .fstPageP {
    font-size: 1.1rem;
  }
  .hero h2 {
    font-size: 1.2rem;
  }
  .hero .adress {
    font-size: 0.9rem;
  }
}
@media (min-width: 500px) and (max-width: 650px) {
  .hero h1 {
    font-size: 2.55rem;
  }
  .hero .fstPageP {
    font-size: 1.4rem;
  }
  .hero h2 {
    font-size: 1.5rem;
  }
  .hero .adress {
    font-size: 1.2rem;
  }
}
@media (min-width: 650px) and (max-width: 768px) {
  .hero h1 {
    font-size: 3.1rem;
  }
  .hero .fstPageP {
    font-size: 1.5rem;
  }
  .hero h2 {
    font-size: 1.6rem;
  }
  .hero .adress {
    font-size: 1.3rem;
  }
}
@media (min-width: 768px) {
  nav {
    gap: 0;
  }
  .fstHead {
    display: flex;
    flex-direction: row;
    justify-content: end;
    background: #434c5e;
  }
  #fstHead, #sndHead {
    padding-right: calc(20% - 100px);
  }
  #fstHead {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding-right: calc(25% - 120px);
  }
  #sndHead {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
    background: #fbfaf8;
    gap: 2rem;
  }
  #sndHead a {
    color: #495057;
  }
  #menuBtn {
    display: none;
  }
  #logo {
    flex: 2;
    height: 3rem;
  }
  #labels {
    flex: 6;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    font-size: 0.8rem;
  }
  #labels #borderLabel {
    color: #a7b7bd;
    border: 3px solid #a7b7bd;
    white-space: nowrap;
    padding: 0.3rem;
  }
  .page1 {
    margin-top: 4rem;
    padding-right: calc(25% - 120px);
    padding-left: calc(25% - 120px);
  }
  .hero h1 {
    font-size: 3.3rem;
  }
  .hero .fstPageP {
    font-size: 1.5rem;
  }
  .hero h2 {
    font-size: 2.2rem;
  }
  .hero .adress {
    font-size: 1.5rem;
  }
  .C1UL {
    display: grid;
    grid-auto-rows: 1fr;
    grid-template-columns: repeat(5, 1fr);
  }
  .C1UL li {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  .C1UL li p {
    text-align: center;
  }
  .C2UL {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
  .viewMore {
    display: none;
  }
